.topbar {
    display: flex;
}

.tb-phone,
.tb-location,
.tb-socials {
    align-items: center;
}

.topbar a {
    color: #fff;
}
.topbar a:hover,
.topbar a:active {
    text-decoration: none;
    color: rgb(159, 245, 192)
}

.spacer {
	flex: 1;
}

.logo2 img {
    /*style={{width: '50px'}}*/
    width: 50px;
    display: inline-flex;
}




/* 
##Device = Desktops
##Screen = 1281px to higher resolution desktops
##Device = Laptops, Desktops
##Screen = B/w 1025px to 1280px
##Device = Tablets, Ipads (portrait)
##Screen = B/w 768px to 1024px
*/

@media (min-width: 769px) {
    .tb-phone, .tb-location {
        display: inline;
    }
    .twins {
        position: absolute;
        top: 15px;
    }
    .tb-location {
        padding: 5px 0px 5px 13px;
    }
    .tb-phone {
        padding: 5px 13px 5px 0px;
        border-right: 1px solid #222;
    }
}

/* 
##Device = Tablets, Ipads (landscape)
##Screen = B/w 768px to 1024px
##Device = Low Resolution Tablets, Mobiles (Landscape)
##Screen = B/w 481px to 767px
##Device = Most of the Smartphones Mobiles (Portrait)
##Screen = B/w 320px to 479px
*/

@media screen and (max-width: 768px) {
    .topbar {
        display: flex;
        position: absolute;
        top: 15px;
        left: 0;
        padding-right: 5px;
        width: 100%;
    }
    .logo2 {
        display: flex;
    }
    .twins {
        display: flex;
        position: absolute;
        left: 110px;
    }
    .tb-phone {
        display: flex;
        width: fit-content;
        padding: 5px 13px 5px 0px;
        border-right: 1px solid #222;
    }
    .tb-location {
        display: flex;
        width: fit-content;
        padding: 5px 0px 5px 13px;
    }
    .spacer {
        flex: 1;
    }
    .tb-socials {
        display: flex;
        width: fit-content;
    }
}

@media screen and (max-width: 499px) {
    .topbar {
        position: absolute;
        left: 0;
        margin-left: 0px;
        top: 0px;
        width: 100%;
        padding: 0px 3px 0px 10px; 
    }
    .tb-phone, .tb-location {
        display: inline;
    }
    .twins {
        position: absolute;
        top: 55px;
        left: 10px;
    }
    .tb-location {
        padding: 5px 0px 5px 13px;
    }
    .tb-phone {
        padding: 5px 13px 5px 0px;
        border-right: 1px solid #222;
    }
    .tb-socials {
        display: inline;
        position: absolute;
        right: 3px;
        top: 18px;
    }
}