.app-container {
    padding: 0px;
    margin: 0px;
    width: 100vw;
}

.app-topbar {
    background-color: #444;
    color: #fff;
    width: inherit;
    height: 56px;
    padding: 3px;
    padding-left: 23px;
    margin-left: 0px;
    margin-right: 0px;
    padding-right: 0px;
}

.menu-button {
    display: inline;
    color: #fff;
    position: absolute;
    left: 10px;
    top: 12px;
    cursor: pointer;
}

header {
    font-size: 12px;
    letter-spacing: .2em;
    text-transform: uppercase;
    line-height: 1.5em;
    background-color: #444;
}

/*For mediums screens like iPad*/
@media (max-width: 768px) {
    .app-topbar {
        height: 70px;
        padding-left: 50px;
        padding-right: 12px;
        top: 0px;
    }
    .logo1 {
        display: none;
    }
    .emptySpace {
        display: none;
    }
    .app-topbar {
        z-index: 100;
    }
    .app-navbar {
        z-index: 300;
    }
}

/*For big screens including iPad Pro*/
@media (min-width: 769px) {
    .menu-button {
        display: none;
    }
    .app-topbar {
        padding-right: 50px;
        padding-left: 260px; 
    }
    .app-topbar div {
        margin-top: 3.5px;
    }
    .page-content {
        position: absolute;
        left: 260px;
    }
    .app-navbar {
        position: absolute !important;
        height: 100vh;
        top: 0;
        width: 230px;
        background-color: #444;
    }
    .emptySpace {
        height: 50px;
    }
    .logo2 {
        display: none;
    }
    .logo1 {
        margin-left: 60px;
    }
    .logo1 img {
        width: 100px;
    }
}

/* For phone screens */
@media screen and (max-width: 499px) {
    .app-topbar {
        height: 90px;
    }
    .app-topbar {
        position: relative;
        z-index: 300;
    }
}

