.side-nav {
    background-color: #444;
    color: #fff;
    margin-top: 70px;
}

.side-nav li{
    list-style: none;
    padding-top: 20px;
    padding-bottom: 20px;
    border-bottom: 1px double #000;
    width: 150px
}

.side-nav li:last-child {
    padding-bottom: 20px;
    border-bottom: none;
}

.side-nav .link {
    color: #fff;
}

.side-nav .link:hover,
.side-nav .link:active {
    color: rgb(159, 245, 192);
    text-decoration: none;
}

@media (max-width: 768px) {
    .side-nav {
        /* display: none; */
    }
}

@media screen and (max-width: 768px) {
    .side-nav {
        position: absolute;
        height: 100%;
        max-width: 70%;
        top: 0px;
        margin: 0;
        box-shadow: 3px 0px 7px rgba(0,0,0,0.5);
        transform: translateX(-100%);
        transition: transform 0.3s ease-out;
        z-index: 200;
    }
    .side-nav.open {
        transform: translateX(0);
    }
    .side-nav ul {
        margin-top: 130px;;
    }
}